* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #D5F3FE;
}

h1 {
  color: #505050;
}

.tweet {
  display: flex;
  background-color: #fff;
  width: 100%;
  border-radius: 4px;
  color: #505050;
}

.timeline {
  padding: 24px 60px;
}

.tweet:not(:last-child) {
  border-bottom: 1px solid #D5F3FE;
}

.icon-container {
  width: 1.5em;
  font-size: 3em;
  padding: 0.2em
}

.body-container {
  flex: 1;
  padding: 0.5em;
}

.account-name {
  color: #868686
}

