.tweet-textarea {
  font-size: 1em;
  width: 100%;
  height: 5em;
  padding: 0.8em;
  resize: vertical;
  margin-top: 16px;
  border: 2px solid rgb(130, 130, 130);
  border-radius: 4px;
}

.send-tweet {
  font-size: 1em;
  padding: 0.5em 1em;
  border: 2px solid #008CBA;
  border-radius: 4px;
  background-color: #008CBA;
  color: white;
  box-shadow: 0 5px #666;
  cursor: pointer;
}

.send-tweet:active {
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.tweet-input {
  padding: 24px 60px;
}